<template>
  <div id="app" v-on:click="hidePanel">
    <!-- 导航栏 -->
    <div class="nav">
      <!-- 版心部分 -->
      <div class="nav-layout">
        <!-- 左边导航栏 logo部分-->
        <div class="nav-left">
          <!-- logo部分 -->
          <router-link to="/index" class="logo">
            <!-- logo图片 -->
            <img src="../static/image/logo.png" alt="">
          </router-link>
        </div>
        <!-- 右边导航条 菜单栏部分-->
        <div class="nav-right">
          <!-- 循环渲染 导航栏 -->
          <router-link v-for="(item, index) in navList" :key="index" :to="item.link">{{item.name}}</router-link>
        </div>
        <!-- 小屏幕内容 -->
        <div class="sm-nav" style="display:none">
          <!-- 菜单图标 -->
          <i class="fa fa-reorder" @click="menuStatus = !menuStatus"></i>
        </div>
        <transition name="slide-fade">
          <!-- 菜单内容 -->
          <div id="menu" class="menu" v-if="menuStatus">
            <!-- 三角形 -->
            <div class="Triangle"></div>
            <!-- 菜单导航栏 -->
            <router-link :to="item.link" v-for="(item, index) in navList" :key="index" @click.native="menuStatus = !menuStatus">
              {{item.name}}
            </router-link>
          </div>
        </transition>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import $ from "jquery";
export default {
  name: "app",
  data() {
    return {
      menuStatus: false,
      // 导航栏列表
      navList: [
        {
          name: "首页",
          link: "/index"
        },
        {
          name: "案例",
          link: "/cases"
        },
        {
          name: "联系我们",
          link: "/contactUs"
        }
      ]
    };
  },
  mounted() {
    $(window).scroll(function() {
      if ($(window).scrollTop() > 50) {
        $(".nav").css({
          "background-color": "black"
        });
      } else {
        $(".nav").css({
          "background-color": "transparent"
        });
      }
    });
  },
  methods: {
    hidePanel: function(event) {
      var menu = document.getElementById("menu");
      if (menu) {
        if (!menu.contains(event.target)) {
          //这句是说如果我们点击到了id为menu以外的区域
          this.menuStatus = false;
        }
      }
    }
  }
};
</script>
